.main-box-left{ width: 800px;
}
.match-nav-box{ background-color: #ffffff;padding: 0 10px; margin-bottom: 20px; -webkit-box-shadow: 0px 1px 10px rgba(0,0,0,0.2); box-shadow: 0px 1px 10px rgba(0,0,0,0.2)
}
.match-nav-box ul li{ float: left;
}
.match-nav-box ul li a{ display: block; padding: 0 15px; height: 52px; line-height: 52px; text-align: center; position: relative;
}
.match-nav-box ul li.active a,.match-nav-box ul li a:hover{ color: #ff5554;
}
.match-nav-box ul li a:after, .match-nav-box ul li a:before { transition: .2s; -webkit-transition: .2s; -moz-transition: .2s; -o-transition: .2s; transform-style: preserve-3d; -webkit-transform-style: preserve-3d; -moz-transform-style: preserve-3d; -o-transform-style: preserve-3d; content: ''; position: absolute; bottom: -2px; width: 0%; height: 2px; background-color: #ff5554;
}
.match-nav-box ul li a:before { right: 50%;
}
.match-nav-box ul li a:after{ left: 50%;
}
.match-nav-box ul li.active a:after, .match-nav-box ul li.active a:before,.match-nav-box ul li:hover a:after, .match-nav-box ul li:hover a:before{ width: 50%;
}
.news-box{ background: #ffffff; padding: 0 20px
}
.news-box ul li{ padding: 20px 0; border-bottom: 1px solid #e5e5e5;
}
.info-img{ display: block; width: 216px; height: 140px; overflow: hidden;
}
.info-img img{
    webkit-transform: scale(1) translateZ(0);
    -webkit-transition: -webkit-transform .75s;
    -webkit-transform: scale(1) translateZ(0);
            transform: scale(1) translateZ(0);
    transition: -webkit-transform .75s;
    transition: transform .75s;
    transition: transform .75s, -webkit-transform .75s;
}
.news-box ul li:hover img{
    -webkit-transform: scale(1.2) translateZ(0);
    -webkit-transition: -webkit-transform .75s cubic-bezier(0,1,.75,1);
    transform: scale(1.2) translateZ(0);
    transition: -webkit-transform .75s cubic-bezier(0,1,.75,1);
    transition: transform .75s cubic-bezier(0,1,.75,1);
    transition: transform .75s cubic-bezier(0,1,.75,1), -webkit-transform .75s cubic-bezier(0,1,.75,1);
}
.info-art{ width: 542px; padding: 10px 15px;
}
.info-art h2{font-size: 20px;  margin-bottom: 10px;  font-weight: 500;
}
.info-art p{ height: 42px; margin-bottom: 15px; overflow: hidden; text-overflow: ellipsis;display: -webkit-box; -webkit-line-clamp: 2; -webkit-box-orient: vertical;  word-break: break-all;
}
.info-art .info-tag span{ margin-right: 10px;
}
.info-art .info-tag span a{ display: inline-block; padding: 0 10px; border-radius: 24px; height: 24px; line-height: 22px; color: #ff5554; border: 1px solid #ff5554;
}
.pagination{ margin: 20px auto;
