/*全局样式*/
body{
    min-width: 1200px;
}
.content{
    width: 1200px;
    margin: 0 auto;
}
.layout-header{
    width: 100%;
    height: 70px;
    background-color: #1a1820;
}
.header-content{
    padding: 10px 0;
}
.logo{
    float: left;
    width: 140px;
    height: 50px;
    background-color: #ff5554;
}
.header-nav{
    float: left;
}
.collect-nav{
    float: right;
}
.header-nav li,.collect-nav li{
    float: left;
    height: 50px;
    line-height: 50px;
    margin: 0 25px;
}
.header-nav li a,.collect-nav li a{
    color: #ffffff;
    font-size: 18px;
    -webkit-transition: all 0.2s;
    transition: all 0.2s
}
.header-nav li.active a{
    color: #f0675e;
}
.header-nav li a:hover,.collect-nav li a:hover{
    color: #ff5554;
}
.footer{
    width: 100%;
    min-width: 120px;
    background: #ffffff;
    padding: 30px 20px;
    margin-top: 50px;
}
.footer-in{
    width: 1200px;
    margin: 0 auto;
    position: relative;
    padding-top: 30px;
}
.friend-link{
    text-align: center;
    word-break: break-all;
    text-align: center;
}
.friend-link a{
    display: inline-block;
    padding: 6px 5px;
}
.cover-mask{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #000;
    opacity: 0;
    filter: alpha(opacity=0);
    -webkit-transition: opacity .5s;
    transition: opacity .5s;
}
.cover-mask-hover:hover .cover-mask{
    opacity: .2;
    filter: alpha(opacity=20);
    -webkit-transition: opacity .5s;
    transition: opacity .5s;
}
.play-btn-hover:hover .play-btn{
    background: 0 0\9;
    filter: progid:dximagetransform.microsoft.alphaimageloader(src="http://imgcache.gtimg.cn/mediastyle/yqq/img/cover_play.png", sizingmethod="crop") alpha(opacity=90);
    opacity: .9;
    -webkit-transform: scale(1);
    -webkit-transition-property: opacity,-webkit-transform;
    -webkit-transition-duration: .5s;
    transform: scale(1);
    transition-property: opacity,-webkit-transform;
    transition-property: opacity,transform;
    transition-property: opacity,transform,-webkit-transform;
    transition-duration: .5s;
    cursor: pointer;
}
.play-btn{
    filter: alpha(opacity=0);
    opacity: 0;
    -webkit-transform: scale(.7);
    -webkit-transition-property: opacity,-webkit-transform;
    -webkit-transition-duration: .5s;
    transform: scale(.7);
    transition-property: opacity,-webkit-transform;
    transition-property: opacity,transform;
    transition-property: opacity,transform,-webkit-transform;
    transition-duration: .5s;
    zoom: 1;
}/**
